.join-blocks {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1rem;
  margin-right: -1rem;

  @include lg {
    margin-left: -.5rem;
    margin-right: -.5rem; }

  @include xs {
    margin-left: rem(-5, 14);
    margin-right: rem(-5, 14); }

  &__block {
    width: 25%;
    padding-left: 1rem;
    padding-right: 1rem;

    @include lg {
      padding-left: .5rem;
      padding-right: .5rem;
      margin-bottom: 1rem; }

    @include sm {
      width: 50%; }

    @include xs {
      padding-left: rem(5, 14);
      padding-right: rem(5, 14);
      margin-bottom: rem(10, 14); } } }

.join-block {
  --bg-color: #fafafa;

  padding: rem(30) rem(30) rem(50);
  background-color: #FAFAFA;
  background-color: var(--bg-color);
  position: relative;
  display: block;
  height: 100%;

  @include tr(background-color .3s, box-shadow .3s);

  @include lg {
    padding: rem(20); }

  @include sm {
    pading: rem(20, 14) rem(6, 14); }

  @include xs {
    text-align: center;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); }

  &:hover {
    --bg-color: #{$white};

    box-shadow: 0px 4px 20px rgba(31, 31, 31, 0.1); }

  &__icon {
    color: $accent;
    margin-bottom: rem(17);
    display: block;
    @include xs {
      width: rem(50, 14);
      height: rem(50, 14);
      border-radius: 50%;

      display: flex;
      align-items: center;
      justify-content: center;

      background-color: $accent;
      color: $white;

      margin-left: auto;
      margin-right: auto;
      margin-bottom: rem(16, 14); }

    .icon {
      font-size: rem(70);
      @include xs {
        font-size: rem(30, 14); } } }

  &__text {
    color: $c-text-aux;
    position: relative;
    display: block;
    z-index: 1;
    @include before {
      position: absolute;
      left: 0;
      right: 0;
      top: .5em;
      height: 1em;
      z-index: 1;
      // background-color: #fafafa
      background-image: linear-gradient(to top, #fafafa, rgba(#fafafa, .6));
      background-image: linear-gradient(to top, var(--bg-color), rgba(#fafafa, .6)); }

    @include xs {
      @include before {
        display: none; } } }

  &__text-in {
    position: relative;
    z-index: 1; }

  &__nmb {
    position: absolute;
    right: 0;
    top: -.5em;
    z-index: 0;
    font-size: em(64);
    line-height: 1;
    opacity: .1;

    font-weight: 700;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 2px currentColor;

    @include xs {
      display: none; } } }
