.section {
  position: relative;
  &__title {
    margin-bottom: rem(20);
    &--mb-md {
      margin-bottom: rem(30); }
    &--mb-lg {
      margin-bottom: rem(40);
      @include md {
        margin-bottom: rem(20); } }
    &--mb-xl {
      margin-bottom: rem(50); } }

  &__text {
    p {
      @include notlast {
        margin-bottom: rem(30); } }
    @include notlast {
      margin-bottom: rem(27); } }

  &__block {
    margin-bottom: rem(50);
    @include md {
      margin-bottom: rem(30); } }

  &__btn {
    text-align: center;
    margin-top: rem(67); } }

.section-list {
  list-style-type: decimal;
  list-style-position: inside;

  &__item {
    position: relative;
    padding-left: rem(25);

    @include notlast {
      margin-bottom: rem(50);
      @include sm {
        margin-bottom: 1rem; } }

    .icon {
      font-size: .8em;
      fill: $accent;
      position: absolute;
      top: .5rem;
      left: 0;

      &--ellipse {
        font-size: em(5); } } } }
