.prev, .next {
  display: flex;
  align-items: center;
  justify-content: center;

  color: #DADADA;
  border-radius: 50%;
  border: 1px solid currentColor;

  width: 1em;
  height: 1em;
  font-size: rem(40);

  @include tr(box-shadow .3s, border-color .3s, color .3s);

  @include hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    border-color: transparent;
    color: $black; }

  .icon {
    font-size: rem(28);
    fill: none;
    stroke: currentColor;
    margin-left: .1em; }

  &.swiper-button-disabled {
    opacity: .5;
    pointer-events: none; } }

.prev {
  transform: scaleX(-1); }

.slider {
  &__wrap {
    position: relative;
    * {
      box-sizing: border-box; } }

  &__pagination {
    text-align: center;
    margin-top: rem(12);

    .swiper-pagination-bullet {
      display: inline-block;

      width: rem(10);
      height: rem(10);

      border-radius: 50%;
      border: 1px solid  #dadada;

      margin-left: rem(4);
      margin-right: rem(4);

      cursor: pointer;

      &.swiper-pagination-bullet-active {
        border-color: $accent;
        background-color: $accent; } } }


  &--items {
    &__wrap {
      .swiper-container {
        padding: 1rem;
        margin-left: -1rem;
        margin-right: -1rem; }

      .slider {
        &__controls {
          position: absolute;
          z-index: 1;
          top: calc(50% - #{rem(20)});
          left: 0;
          width: 100%;
          height: 1px;
          transform: translate(0, -50%); }

        &__prev, &__next {
          position: absolute;
          top: 50%;
          transform: translate(0, -50%);

          @include lg {
            display: none; } }

        &__prev {
          left: 0;
          transform: translate(0, -50%) scaleX(-1); }
        &__next {
          right: 0; } } } } }
