.hero {
  background-color: $accent;
  @include image('bg-hero.jpg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  color: $white;
  position: relative;

  &__slider {
    padding-top: rem(190);
    padding-bottom: rem(126);
    position: relative;
    z-index: 3;

    @include xs {
      padding-top: rem(60, 14);
      padding-bottom: rem(150, 14); }

    .slider {
      &__prev, &__next {
        color: $white;
        margin-left: rem(10);
        margin-right: rem(10);
        @include hover {
          color: $white; } } } }

  &__title-block {
    max-width: percentage(7 / 12);
    margin-left: 0;
    margin-right: 0;
    @include xs {
      max-width: none; } }

  &__controls {
    display: flex; }

  &__title {
    margin-bottom: rem(20); }

  &__subttl {
    margin-bottom: rem(50); }

  &__btn {
    margin-bottom: rem(63); }

  &__laptop {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;
    width: 42%;

    @include sm {
      width: 50%; }
    @include xs {
      width: 55%; } }

  &__bottom-img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 0; }

  &__bottom-circle {
    position: absolute;
    bottom: rem(-200);
    left: 0;
    z-index: 1;
    @include md {
      display: none; } } }

.hero-laptop {
  text-align: right;

  &__bg {
    position: relative;
    z-index: 0; }

  &__slide-container {
    position: absolute;
    z-index: 1;
    width: 82%;
    height: 81%;
    top: 7%;
    right: 0;

    overflow: hidden;

    .swiper-container {
      height: 100%; } }

  &__slide {
    // +coverdiv
 } }    // object-fit: cover
