.social {
  &__item {
    display: inline-block;
    @include notlast {
      margin-right: rem(20);
      @include lg {
        margin-right: rem(10); }
      @include sm {
        margin-right: rem(4, 14); } } }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;

    background-color: #30353D;
    border-radius: 50%;

    width: 1em;
    height: 1em;
    font-size: rem(45);

    @include tr(color .3s);

    @include hover {
      color: $accent-aux; }

    @include lg {
      font-size: rem(30); }
    @include sm {
      font-size: rem(21, 14); }

    .icon {
      font-size: rem(18);
      @include lg {
        font-size: rem(10); } } } }
