.header {
  background-color: $accent;
  color: $white;

  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;

  @include tr(background-color .3s);

  &__logo {
    width: rem(181);
    margin-right: rem(174);
    margin-left: rem(-60);
    @include xl {
      margin-left: 0; }
    @include lg {
      width: rem(100);
      margin-right: 0; }
    @include sm {
      width: rem(65, 14); } }

  &__inner {
    display: flex;
    align-items: center;

    height: $header-height;

    @include lg {
      height: $header-height-lg; } }

  &__nav {
    flex-grow: 1;
    @include lg {
      padding: rem(24) rem(10) rem(54);
      background-color: $black;
      width: percentage(160 / 320);
      position: absolute;
      z-index: 10;
      top: 0;
      right: 0;
      transform: translate(100%, 0);
      @include ready {
        @include tr(transform .5s $easeOutQuart); }

      &--open {
        transform: translate(0, 0); } } }

  &__btn {
    @include lg {
      display: none; } }

  &__burger, &__close, &__sign-up {
    @include lgmin {
      display: none; } }

  &__sign-up {
    @include tr(color .3s);
    @include hover {
      color: $accent-aux; } }

  &__burger {
    display: block;
    margin-left: auto; }



  &--transp {
    background-color: transparent; }

  &--has-background {
    background-color: $accent; } }

