.input {
  #{$text-inputs} {
    height: rem(50);
    width: 100%;

    padding: rem(10) rem(20);
    background-color: transparent;
    border-bottom: 1px solid currentColor;
    @include placeholder {
      color: inherit; }

    @include xs {
      height: rem(30, 14);
      padding: rem(5, 14) rem(15, 14); } } }
