.footer {
  background-color: $black;
  color: $white;
  padding-top: rem(65);
  padding-bottom: rem(166);

  @include lg {
    padding-top: rem(20);
    padding-bottom: rem(20); }
  @include sm {
    padding-top: rem(10, 14);
    padding-bottom: rem(10, 14); }

  &__inner {
    display: flex;
    justify-content: space-between;
    @include lg {
      align-items: center; } }

  &__right {
    display: flex; }

  &__logo {
    @extend .title;
    @extend .title--h2;
    margin-bottom: rem(18);
    @include sm {
      margin-bottom: rem(3, 14); } }

  &__copy {
    font-size: em(16); }

  &__nav {
    min-width: 350px;
    @include lg {
      display: none; } }

  &--sm {
    padding-top: rem(20);
    padding-bottom: rem(20); } }
