.nav {
  $this: &;

  font-size: em(16);

  @include sm {
    font-size: 1em; }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-bottom: rem(10); }

  &__item {
    display: inline-block;
    @include notlast {
      margin-right: rem(77);
      @include lg {
        margin-right: 0; } }
    @include lg {
      display: block; }

    &--current {
      text-decoration: underline; } }

  &__link {
    @include lg {
      display: block;
      padding: rem(7) 0;
      border-bottom: 1px solid rgba($white, .2); }

    @include hover {
      @extend #{$this}__item--current; } }


  &--footer {
    #{$this}__list {
      display: flex;
      flex-wrap: wrap; }

    #{$this}__item {
      margin-right: 0 !important;
      width: 50%;
      margin-bottom: rem(10); }

    #{$this}__link {
      text-decoration: underline; } } }
